import { Component, OnInit, OnDestroy } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import { validateEmail } from './../../../shared/utils';
import { GendersService } from './../../../core/services/references/genders.service';
import { UserService, UserInfos } from './../../../core/services/user.service';
import { PopupService, PopupOptions } from './../../../core/services/popup.service';
import { CreateRequestService } from './../../../core/services/create-request.service';
import { LangService } from './../../../core/services/references/lang.service';
import { Subscription, Observable, combineLatest } from 'rxjs';
import { RelationshipsService } from '@app/modules/core/services/references/relationships.service';
import { distinctUntilChanged } from 'rxjs/operators';
import { NgxSpinnerService } from 'ngx-spinner';
import { ButtonService } from "../../../shared/services/button.service";

@Component({
    selector: 'itx-helpdesk-create-contact',
    templateUrl: './helpdesk-create-contact.component.html',
    styleUrls: ['./helpdesk-create-contact.component.scss']
})
export class HelpdeskCreateContactComponent implements OnInit, OnDestroy {

    public disableBtn: boolean = false;
    public translation;
    public genderList: any[];
    public relationshipsList: any[];
    public helpdeskForm: FormGroup;
    public errorMsgTop = false;
    public isFormNoModified = false;
    public showGender = true;
    public showRelationship  = true;

    public emailPers: FormControl;
    public phonePers: FormControl;
    public mobilePers: FormControl;
    public emailPro: FormControl;
    public phonePro: FormControl;
    public mobilePro: FormControl;
    public firstname: FormControl;
    public lastname: FormControl;
    public genderEmer: FormControl;
    public relationships: FormControl;
    public emailEmer: FormControl;
    public phoneEmer: FormControl;

    public userInfos: UserInfos;

    private sendRequestSubscription: Subscription;
    private combinedObs: Observable<any>;
    private showPopup: boolean;
    showSpinner1: boolean;

    constructor(
        private fb: FormBuilder,
        private gendersService: GendersService,
        private popupService: PopupService,
        private userService: UserService,
        private createRequestService: CreateRequestService,
        private router: Router,
        private langService: LangService,
        private relationshipsService: RelationshipsService,
        private spinner: NgxSpinnerService,
        private button: ButtonService
    ) {
        this.translation = this.langService.lang;
        this.showPopup = true;

        this.emailPers = new FormControl(null, [Validators.required], [validateEmail.bind(this.emailPers)]);
        this.phonePers = new FormControl(null, [Validators.required]);
        this.mobilePers = new FormControl(null, [Validators.required]);
        this.emailPro = new FormControl(null, [Validators.required], [validateEmail.bind(this.emailPro)]);
        this.phonePro = new FormControl(null, [Validators.required]);
        this.mobilePro = new FormControl(null, [Validators.required]);
        this.firstname = new FormControl(null, [Validators.required]);
        this.lastname = new FormControl(null, [Validators.required]);
        this.genderEmer = new FormControl(null, [Validators.required, this.validSelect]);
        this.relationships = new FormControl(null, [Validators.required, this.validSelect]);
        this.emailEmer = new FormControl(null, []);
        this.phoneEmer = new FormControl(null, [Validators.required]);
        this.helpdeskForm = this.fb.group({
            EmailAddressPersonalContactInfo: this.emailPers,
            PhoneNumberPersonalContactInfo: this.phonePers,
            MobileNumberPersonalContactInfo: this.mobilePers,
            EmailAddressProfessionalContactInfo: this.emailPro,
            PhoneNumberProfessionalContactInfo: this.phonePro,
            MobileNumberProfessionalContactInfo: this.mobilePro,
            FirstNameEmergencyContact: this.firstname,
            GenderEmergencyContact: this.genderEmer,
            RelationshipEmergencyContact: this.relationships,
            LastNameEmergencyContact: this.lastname,
            EmailAddressEmergencyContact: this.emailEmer,
            PhoneNumberEmergencyContact: this.phoneEmer
        });

        this.helpdeskForm.statusChanges.subscribe(status => {
            this.showMsg();
        });

        this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
            if (this.helpdeskForm.valid) {
                const checkRequestMsg: string = localStorage.getItem('checkRequestMsg');
                if (!res.popup || checkRequestMsg === '1') {
                    this.sendRequest();
                }
            } else {
                this.errorMsgTop = true;
                this.popupService.setPopupState(false);
                this.markAllDirty(this.helpdeskForm);
                window.scrollTo(0, 0);
            }
        });

        this.combinedObs = combineLatest(
            this.gendersService.genders$,
            this.relationshipsService.getRelationships(),
            this.userService.userInfos$
        );

        this.combinedObs.subscribe(([genders, relationships, userInfos]) => {
            this.genderList = genders;
            this.relationshipsList = relationships;
            this.userInfos = userInfos;
            this.updateFormVal();
            this.hideSpinner();
            this.button.changeMessage(false);
        });
    }

    ngOnInit(): void {
        setTimeout(() => this.showSpinner(), 25);
        this.gendersService.getGenders();
        this.userService.getUserInfos().subscribe();
        this.makeFieldOptional(this.mobilePers, this.phonePers);
        this.makeFieldOptional(this.mobilePro, this.phonePro);
        this.emailEmer.valueChanges.pipe(distinctUntilChanged()).subscribe(
            (data) => {
                if (data && data.length > 0) {
                    this.emailEmer.setValidators([Validators.email]);
                } else {
                    this.emailEmer.clearValidators();
                }
                this.emailEmer.updateValueAndValidity();
            }
        );
        this.button.changeMessage(true);
    }

    showSpinner(){
        this.showSpinner1 = true;
      }
      
      hideSpinner(){
        this.showSpinner1 = false;
      }

    ngOnDestroy(): void {
        this.sendRequestSubscription.unsubscribe();
    }

    private validSelect(c: FormControl): any {
        const invalid: boolean = c.value === 0 || c.value === 'none' ? true : false;
        return invalid ? { 'invalid': { value: c.value } } : null;
    }

    private makeFieldOptional(control1: FormControl, control2: FormControl): void {
        control1.valueChanges.pipe(distinctUntilChanged()).subscribe((data) => {
            if (data && data.length > 0) {
                control2.clearValidators();
            } else {
                control2.setValidators([Validators.required]);
            }
            control2.updateValueAndValidity();
        });

        control2.valueChanges.pipe(distinctUntilChanged()).subscribe((data) => {
            if (data && data.length > 0) {
                control1.clearValidators();
            }
            else {
                control1.setValidators([Validators.required]);
            }
            control1.updateValueAndValidity();

        });
    }

    public showMsg(): void {
        if (this.helpdeskForm.valid) {
            this.errorMsgTop = false;
        }
    }

    private markAllDirty(control: AbstractControl): void {
        if (control.hasOwnProperty('controls')) {
            control.markAsDirty();
            const ctrl: any = <any>control;
            for (const inner in ctrl.controls) {
                if (ctrl.controls[inner]) {
                    this.markAllDirty(ctrl.controls[inner] as AbstractControl);
                }
            }
        } else {
            (<FormControl>(control)).markAsDirty();
        }
    }

    private updateFormVal(): void {
        this.userInfos.EmergencyContact.Gender = this.genderList.filter((item) => {
            if (this.userInfos.EmergencyContact.Gender) {
                return item.ID === this.userInfos.EmergencyContact.Gender.ID;
            }
            return false;
        })[0];
        this.userInfos.EmergencyContact.Relationship = this.relationshipsList.filter((item) => {
            if (this.userInfos.EmergencyContact.Relationship) {
                return item.ID === this.userInfos.EmergencyContact.Relationship.ID;
            }
            return false;
        })[0];
        let genderEmergencyContact = this.userInfos.EmergencyContact.Gender ? this.userInfos.EmergencyContact.Gender.ID : 0;
        let relationshipEmergencyContact = this.userInfos.EmergencyContact.Relationship ? this.userInfos.EmergencyContact.Relationship.ID : 0;

        this.helpdeskForm.patchValue({
            EmailAddressPersonalContactInfo: this.userInfos.PersonalContactInfo.EmailAddress,
            PhoneNumberPersonalContactInfo: this.userInfos.PersonalContactInfo.PhoneNumber,
            MobileNumberPersonalContactInfo: this.userInfos.PersonalContactInfo.MobileNumber,
            EmailAddressProfessionalContactInfo: this.userInfos.ProfessionalContactInfo.EmailAddress,
            PhoneNumberProfessionalContactInfo: this.userInfos.ProfessionalContactInfo.PhoneNumber,
            MobileNumberProfessionalContactInfo: this.userInfos.ProfessionalContactInfo.MobileNumber,
            GenderEmergencyContact: genderEmergencyContact,
            RelationshipEmergencyContact: relationshipEmergencyContact,
            FirstNameEmergencyContact: this.userInfos.EmergencyContact.FirstName,
            LastNameEmergencyContact: this.userInfos.EmergencyContact.LastName,
            EmailAddressEmergencyContact: this.userInfos.EmergencyContact.EmailAddress,
            PhoneNumberEmergencyContact: this.userInfos.EmergencyContact.PhoneNumber
        });
    } catch (e) { console.error(e); }

    private constructObj(isFullObjectNeeded: boolean): any {
        let defaultObj = {
            PersonalContactInfo: {
                EmailAddress: this.helpdeskForm.value.EmailAddressPersonalContactInfo,
                PhoneNumber: this.helpdeskForm.value.PhoneNumberPersonalContactInfo,
                MobileNumber: this.helpdeskForm.value.MobileNumberPersonalContactInfo
            },
            ProfessionalContactInfo: {
                EmailAddress: this.helpdeskForm.value.EmailAddressProfessionalContactInfo,
                PhoneNumber: this.helpdeskForm.value.PhoneNumberProfessionalContactInfo,
                MobileNumber: this.helpdeskForm.value.MobileNumberProfessionalContactInfo
            },
            EmergencyContact: {
                Gender: this.genderList.filter(el => el.ID == this.helpdeskForm.value.GenderEmergencyContact)[0],
                Relationship: this.relationshipsList.filter(el => el.ID == this.helpdeskForm.value.RelationshipEmergencyContact)[0],
                FirstName: this.helpdeskForm.value.FirstNameEmergencyContact,
                LastName: this.helpdeskForm.value.LastNameEmergencyContact,
                EmailAddress: this.helpdeskForm.value.EmailAddressEmergencyContact,
                PhoneNumber: this.helpdeskForm.value.PhoneNumberEmergencyContact
            }
        };

        let defFullObj = JSON.parse(JSON.stringify(defaultObj));
        for (const key in defaultObj) {
            if (defaultObj.hasOwnProperty(key)) {
                const cat: any = defaultObj[key];

                for (const scndKey in cat) {
                    if (cat.hasOwnProperty(scndKey)) {
                        const property: any = cat[scndKey];
                        if (scndKey === 'Gender' || scndKey === 'Relationship') {
                            const currentObject = this.userInfos[key][scndKey] ? this.userInfos[key][scndKey].ID : 0;
                            if (this.helpdeskForm.value[scndKey + key] === currentObject) {
                                delete defaultObj[key][scndKey];
                            }
                        } else {
                            if (this.helpdeskForm.value[scndKey + key] === this.userInfos[key][scndKey]) {
                                delete defaultObj[key][scndKey];
                            }
                        }
                    }
                }

            }
        }
        // If full Object is not needed then assign defaultObj to defFullObj which has only changed values.
        if (!isFullObjectNeeded)
            defFullObj = defaultObj;

        // Returns object that only tracks changes and (full Obj, if isFullObjectNeeded is true )
        return [defaultObj, defFullObj];
    }

    public sendRequest(): any {
        const [contactInfosTrack, contactInfos] = this.constructObj(true);

        let isFormModified = false;
        for (const key in contactInfosTrack) {
            if (Object.keys(contactInfosTrack[key]).length > 0) {
                isFormModified = true;
                break;
            }
        }

        if (isFormModified === false) {
            this.isFormNoModified = true;
            window.scrollTo(0, 0);
            return;
        }

        this.createRequestService.requestInProgress(true);

        this.userService.changeContact(contactInfos).subscribe(
            (res) => {
                this.createRequestService.requestInProgress(false);
                this.router.navigate(['/']);
            },
            (err) => {
                this.createRequestService.requestInProgress(false);
                const popupOptions: PopupOptions = {
                    title: this.translation.createRequest.contact.popInErr.title,
                    text: err.Description,
                    bgState: false,
                    arrow: false,
                    timer: 4000,
                    position: {
                        bottom: '14vh'
                    }
                };
                this.popupService.setPopupState(true, popupOptions);
            }
        );
    }
}
