import { Component, OnInit, OnDestroy, AfterContentChecked } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';

import { GendersService } from './../../../core/services/references/genders.service';
import { CreateRequestService } from './../../../core/services/create-request.service';
import { PopupService, PopupOptions } from './../../../core/services/popup.service';
import { UserService, UserInfos, NewNationalityObj } from './../../../core/services/user.service';
import { LangService } from './../../../core/services/references/lang.service';
import { Child } from '../../../shared/models/child';
import { FileValidator } from '../../../shared/directives/file-validator.directive';
import { FileSizeValidator } from '@app/modules/shared/directives/file-size-validator.directive';
import { CountriesService } from '@app/modules/core/services/references/countries.service';
import { Gender } from '@app/modules/shared/models/gender';
import { distinctUntilChanged } from 'rxjs/internal/operators/distinctUntilChanged';
import { NgxSpinnerService } from 'ngx-spinner';
import { ButtonService } from "../../../shared/services/button.service";

@Component({
  selector: 'itx-helpdesk-create-children',
  templateUrl: './helpdesk-create-children.component.html',
  styleUrls: ['./helpdesk-create-children.component.scss']
})
export class HelpdeskCreateChildrenComponent implements OnInit, OnDestroy, AfterContentChecked {

  public translation: any;
  public disableBtn: boolean = false;
  public genderList: Gender[];
  public countryList: any[];
  public childrenList: any[] = [];
  public helpdeskForm: FormGroup;
  public helpdeskFormDelete: FormGroup;

  public childrenIdToDel: number;
  public errorMsgTop: boolean = false;
  public errorMsgTopDel: boolean = false;

  public childChoice: FormControl;
  public childrenID: FormControl;
  public reason: FormControl;
  public gender: FormControl;
  public firstname: FormControl;
  public lastname: FormControl;
  public birthdate: FormControl;
  public nationality: FormControl;
  public isExpatriate: FormControl;
  public isDependant: FormControl;
  public passportNumber: FormControl;
  public passportExpDate: FormControl;
  public file: FormControl;

  private attachments: any;

  private sendRequestSubscription: Subscription;
  private showPopup: boolean;
  showSpinner1: boolean;

  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private createRequestService: CreateRequestService,
    private gendersService: GendersService,
    private popupService: PopupService,
    private router: Router,
    private route: ActivatedRoute,
    private langService: LangService,
    private countriesService: CountriesService,
    private spinner: NgxSpinnerService,
    private button: ButtonService
  ) {
    this.translation = this.langService.lang;
    this.showPopup = true;

    this.childChoice = new FormControl('add', [Validators.required]);
    this.childrenID = new FormControl(null, [Validators.required]);
    this.reason = new FormControl(null, [Validators.required]);
    this.gender = new FormControl(null, [Validators.required]);
    this.firstname = new FormControl(null, [Validators.required]);
    this.lastname = new FormControl(null, [Validators.required]);
    this.birthdate = new FormControl(null, [Validators.required]);
    this.nationality = new FormControl(null, [Validators.required]);
    this.isExpatriate = new FormControl('no', [Validators.required]);
    this.isDependant = new FormControl('no', [Validators.required]);
    this.passportNumber = new FormControl(null, []);
    this.passportExpDate = new FormControl(null, []);
    this.file = new FormControl("false", [FileValidator.validate, FileSizeValidator.validate]);
    this.helpdeskForm = this.fb.group({
      Gender: this.gender,
      FirstName: this.firstname,
      LastName: this.lastname,
      Nationality: this.nationality,
      IsDependant: this.isDependant,
      IsExpatriate: this.isExpatriate,
      Birthdate: this.birthdate,
      PassportNo: this.passportNumber,
      ExpirationDate: this.passportExpDate,
      Files: this.file
    });



    this.helpdeskForm.statusChanges.subscribe(status => {
      this.showMsg();
    });

    this.helpdeskFormDelete = this.fb.group({
      ChildID: this.childrenID,
      Reason: this.reason
    });

    this.helpdeskFormDelete.statusChanges.subscribe(status => {
      this.showMsg();
    });

    this.countriesService.getCountries().subscribe(res => {
      this.countryList = res;
    });

    this.route.params.subscribe(params => {
      if (params['id']) {
        this.childrenIdToDel = params['id'];
        this.childChoice.setValue('delete');
        this.childrenID.setValue(this.childrenIdToDel);
      }
    });



    this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
      let currentForm: any = null;
      if (this.childChoice.value === 'add') {

        currentForm = this.helpdeskForm;
      } else if (this.childChoice.value === 'delete') {
        currentForm = this.helpdeskFormDelete;
      }

      this.setUserCategoryValidators();

      if (currentForm.valid) {
        const checkRequestMsg: string = localStorage.getItem('checkRequestMsg');
        if (!res.popup || checkRequestMsg === '1') {
          this.sendRequest();
          
        }
      } else {
        this.errorMsgTop = true;
        this.errorMsgTopDel = true;
        this.popupService.setPopupState(false);
        this.markAllDirty(currentForm);
        window.scrollTo(0, 0);
      }
    });

    this.gendersService.genders$.subscribe(res => {
      this.genderList = res;
    });

    this.userService.userInfos$.subscribe(userInfos => {
      this.getChildrenList(userInfos);
      this.hideSpinner();
      this.button.changeMessage(false);
    });
  }

  ngOnInit(): void {
    setTimeout(() => this.showSpinner(), 25);
    this.gendersService.getGenders();
    this.userService.getUserInfos().subscribe();
    this.button.changeMessage(true);
  }


  showSpinner(){
    this.showSpinner1 = true;
  }
  
  hideSpinner(){
    this.showSpinner1 = false;
  }

  ngAfterContentChecked(): void {
    this.makeFieldMandatory(this.passportNumber, this.passportExpDate);
    this.makeFieldMandatory(this.passportExpDate, this.passportNumber);
  }

  ngOnDestroy(): void {
    this.sendRequestSubscription.unsubscribe();
  }



  setUserCategoryValidators() {
    const PassportNo = this.helpdeskForm.get('PassportNo').value;
    const ExpirationDate = this.helpdeskForm.get('ExpirationDate').value;

    let dataxx = this.helpdeskForm.get('Files').value;

 

    if ((PassportNo != null || ExpirationDate != null) && dataxx=="false") {

      this.helpdeskForm.patchValue({ Files: null });
      
    }
    if ((PassportNo == null || PassportNo=="") && (ExpirationDate == null || ExpirationDate=="")) {
      this.helpdeskForm.patchValue({ Files: "false" });
      
    }
    
  }

  buttonclick(){

    this.helpdeskForm.get('ExpirationDate').setValue('');

}
buttonclick1(){

    this.helpdeskForm.get('PassportNo').setValue('');

}

  public getChildrenList(userInfos: UserInfos): void {


    this.childrenList = [];
    userInfos.Children.forEach(children => {
      const temp: any = {
        ID: children.ID,
        Name: children.FirstName + ' ' + children.LastName
      };
      this.childrenList.push(temp);
    });
  }

  public getFilesToUpload(files: any): void {
    this.attachments = files;
  }

  public showMsg(): void {
    if (this.helpdeskForm.valid) {
      this.errorMsgTop = false;
    } else if (this.helpdeskFormDelete.valid) {
      this.errorMsgTopDel = false;
    }
  }

  private markAllDirty(control: AbstractControl): void {
    if (control.hasOwnProperty('controls')) {
      control.markAsDirty();
      const ctrl: any = <any>control;
      for (const inner in ctrl.controls) {
        if (ctrl.controls[inner]) {
          this.markAllDirty(ctrl.controls[inner] as AbstractControl);
        }
      }
    } else {
      (<FormControl>(control)).markAsDirty();
    }
  }

  private makeFieldMandatory(control1: FormControl, control2: FormControl): void {
    control1.valueChanges.pipe(distinctUntilChanged()).subscribe((data) => {
      if (data && data.length > 0) {
        control2.setValidators([Validators.required]);
      } else {
        control2.clearValidators();
      }
      control2.updateValueAndValidity();
    });

    control2.valueChanges.pipe(distinctUntilChanged()).subscribe((data) => {
      if (data && data.length > 0) {
        control1.setValidators([Validators.required]);
      } else {
        control1.clearValidators();
      }
      control1.updateValueAndValidity();
    });
  }

  public sendRequest(): void {

    
    this.createRequestService.requestInProgress(true);

    if (this.childChoice.value === 'add') {
      const isExpatriate = this.isExpatriate.value === 'yes' ? true : false;
      const isDependant = this.isDependant.value === 'yes' ? true : false;
      const currentNationality: NewNationalityObj = this.countryList.find(country => country.Code === this.nationality.value)
      const newChild: Child = {
        Gender: this.genderList.filter(res => res.ID == this.gender.value)[0],
        FirstName: this.firstname.value,
        LastName: this.lastname.value,
        Birthdate: this.birthdate.value,
        Nationality: currentNationality,
        IsExpatriate: isExpatriate,
        IsDependant: isDependant,
        Passport: {
          PassportNo: this.passportNumber.value,
          ExpirationDate: this.passportExpDate.value
        }
      };

      this.userService.addChild(newChild, this.attachments).subscribe(
        (res) => {
          this.createRequestService.requestInProgress(false);
          this.router.navigate(['/']);
        },
        (err) => {
          console.log('ERROR', err);
          this.createRequestService.requestInProgress(false);
          const popupOptions: PopupOptions = {
            title: this.translation.createRequest.childrens.popInErr.titleAdd,
            text: err.Description,
            bgState: false,
            arrow: false,
            timer: 4000,
            position: {
              bottom: '14vh'
            }
          };
          this.popupService.setPopupState(true, popupOptions);
        }
      );
    } else if (this.childChoice.value === 'delete') {
      this.userService.deleteChild(this.childrenID.value, this.reason.value).subscribe(
        (res) => {
          this.createRequestService.requestInProgress(false);
          this.router.navigate(['/']);
        },
        (err) => {
          console.log('ERROR', err);
          this.createRequestService.requestInProgress(false);
          const popupOptions: PopupOptions = {
            title: this.translation.createRequest.childrens.popInErr.titleRemove,
            text: err.Description,
            bgState: false,
            arrow: false,
            timer: 4000,
            position: {
              bottom: '14vh'
            }
          };
          this.popupService.setPopupState(true, popupOptions);
        }
      );
    }
  }
}
