import { Component, OnInit, OnDestroy } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';

import { UserService, NewNationalityObj } from './../../../core/services/user.service';
import { PopupService, PopupOptions } from './../../../core/services/popup.service';
import { CurrenciesService } from './../../../core/services/references/currencies.service';
import { CountriesService } from './../../../core/services/references/countries.service';
import { PriorityLevelsService } from './../../../core/services/references/priority-levels.service';
import { CreateRequestService } from './../../../core/services/create-request.service';
import { LangService } from './../../../core/services/references/lang.service';
import { FileValidator } from '../../../shared/directives/file-validator.directive';
import { FileSizeValidator } from '@app/modules/shared/directives/file-size-validator.directive';
import { BicValidatorDirective } from '@app/modules/shared/directives/bic-validator.directive';
import { IbanValidatorDirective } from '@app/modules/shared/directives/iban-validator.directive';
import { Subscription } from 'rxjs';
import { BankAccountService } from '@app/modules/core/services/references/bank-account.service';

@Component({
  selector: 'itx-helpdesk-create-bank',
  templateUrl: './helpdesk-create-bank.component.html',
  styleUrls: ['./helpdesk-create-bank.component.scss']
})
export class HelpdeskCreateBankComponent implements OnInit, OnDestroy {

  public translation;

  public countryList;
  public currencyList;
  public bankAccountTypes;

  public bankIdToDel: number;
  public errorMsgTop: boolean = false;
  public errorMsgTopDel: boolean = false;

  public priorityLevels: string[];
  public helpdeskForm: FormGroup;
  public helpdeskFormDelete: FormGroup;
  public bankChoice: FormControl;
  public bankAccountTypesControl: FormControl;
  public routingNumber: FormControl;
  public accountNumber: FormControl;
  public reason: FormControl;
  public bankAccountID: FormControl;
  public date: FormControl;
  public bankName: FormControl;
  public country: FormControl;
  public bic: FormControl;
  public iban: FormControl;
  public beneficiary: FormControl;
  public currency: FormControl;
  public comment: FormControl;
  public file: FormControl;

  public userInfos: any;
  public bankAccounts: any[] = [];

  private attachments;

  private sendRequestSubscription: Subscription;
  private currenciesSub: Subscription;
  private priorityLvlSub: Subscription;
  private countriesSub: Subscription;
  private userInfoSub: Subscription;
  private bankAccountTypesSub: Subscription;
  private showPopup: boolean;

  constructor(
    private fb: FormBuilder,
    private priorityLevelsService: PriorityLevelsService,
    private createRequestService: CreateRequestService,
    private popupService: PopupService,
    private userService: UserService,
    private currenciesService: CurrenciesService,
    private countriesService: CountriesService,
    private router: Router,
    private route: ActivatedRoute,
    private langService: LangService,
    private bankAccountTypesService: BankAccountService
  ) {
    this.translation = this.langService.lang;
    this.showPopup = true;

    this.bankChoice = new FormControl('add', [Validators.required]);
    this.bankAccountID = new FormControl(null, [Validators.required]);
    this.reason = new FormControl(null, [Validators.required]);
    this.date = new FormControl(null, [Validators.required]);
    this.bankName = new FormControl(null, [Validators.required]);
    this.bankAccountTypesControl = new FormControl(null, [Validators.required]);
    this.routingNumber = new FormControl(null);
    this.accountNumber = new FormControl(null, [Validators.required]);
    this.country = new FormControl(null, [Validators.required]);
    this.bic = new FormControl(null, [BicValidatorDirective.validate]);
    this.iban = new FormControl(null, [IbanValidatorDirective.validate]);
    this.beneficiary = new FormControl(null, [Validators.required]);
    this.currency = new FormControl(null, [Validators.required]);
    this.comment = new FormControl(null);
    this.file = new FormControl(null, [FileValidator.validate, FileSizeValidator.validate]);

    this.helpdeskForm = this.fb.group({
      StartDate: this.date,
      BankName: this.bankName,
      BankType: 0,
      RoutingNumber: this.routingNumber,
      AccountNumber: this.accountNumber,
      Country: this.country,
      BIC: this.bic,
      IBAN: this.iban,
      Beneficiary: this.beneficiary,
      Currency: this.currency,
      Comment: this.comment,
      File: this.file
    });

    // Listen to Account type control changes in order to implement conditionnal validators on BIC control
    this.bankAccountTypesControl.valueChanges.subscribe(value => {
      if (value === '1') { // IBA
        this.bic.setValidators(BicValidatorDirective.validate);
        this.iban.setValidators(IbanValidatorDirective.validate);
        this.accountNumber.setValidators([]);
      } else {
        this.bic.setValidators([]);
        this.iban.setValidators([]);
        this.accountNumber.setValidators(Validators.required);
      }
      this.accountNumber.updateValueAndValidity();
      this.bic.updateValueAndValidity();
      this.iban.updateValueAndValidity();
    });

    this.bankAccountTypesSub = this.bankAccountTypesService.getBankAccountTypes().subscribe(res => {
      this.bankAccountTypes = res;
    });

    this.helpdeskForm.statusChanges.subscribe(status => {
      this.showMsg();
    });

    this.helpdeskFormDelete = this.fb.group({
      BankAccountID: this.bankAccountID,
      Reason: this.reason
    });

    this.helpdeskFormDelete.statusChanges.subscribe(status => {
      this.showMsg();
    });

    this.route.params.subscribe(params => {
      if (params['id']) {
        this.bankIdToDel = params['id'];
        this.bankChoice.setValue('delete');
        this.bankAccountID.setValue(this.bankIdToDel);
      }
    });

    this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
      let currentForm: any = null;
      if (this.bankChoice.value === 'add') {
        currentForm = this.helpdeskForm;
      } else if (this.bankChoice.value === 'delete') {
        currentForm = this.helpdeskFormDelete;
      }

      if (currentForm.valid) {
        const checkRequestMsg: string = localStorage.getItem('checkRequestMsg');
        if (!res.popup || checkRequestMsg === '1') {
          this.sendRequest();
        }
      } else {
        this.errorMsgTop = true;
        this.errorMsgTopDel = true;
        this.popupService.setPopupState(false);
        this.markAllDirty(currentForm);
        window.scrollTo(0, 0);
      }
    });

    this.currenciesSub = this.currenciesService.currencies$.subscribe(list => {
      this.currencyList = list;
    });

    this.priorityLvlSub = this.priorityLevelsService.priorityLevels$.subscribe(res => {
      this.priorityLevels = res;
    });

    this.countriesSub = this.countriesService.countries$.subscribe(countries => {
      this.countryList = countries;
    });

    this.userInfoSub = this.userService.userInfos$.subscribe(userInfos => {
      this.userInfos = userInfos;
      this.getBankAccountsList();
    });
  }

  ngOnInit(): void {
    this.priorityLevelsService.getPriorityLevels();
    this.currenciesService.getCurrencies().subscribe();
    this.countriesService.getCountries().subscribe();
    this.userService.getUserInfos().subscribe();
  }

  ngOnDestroy(): void {
    this.sendRequestSubscription.unsubscribe();
    this.countriesSub.unsubscribe();
    this.currenciesSub.unsubscribe();
    this.priorityLvlSub.unsubscribe();
    this.userInfoSub.unsubscribe();
    this.bankAccountTypesSub.unsubscribe();
  }

  public getBankAccountsList(): void {
    this.userInfos.BankAccounts.forEach(bankAccount => {
      let temp;
      if (bankAccount.BankType.Name.toLowerCase() === 'bankaccount') {
          temp = {
              'ID': bankAccount.ID,
              'Name': bankAccount.BankName + ', ' + bankAccount.AccountNumber + ' - ' + bankAccount.Currency
          };
      } else {
          temp = {
              'ID': bankAccount.ID,
              'Name': bankAccount.BankName + ', ' + bankAccount.IBAN + ' - ' + bankAccount.Currency
          };
      }
      this.bankAccounts.push(temp);
    });
  }

  public getFilesToUpload(files: any): void {
    this.attachments = files;
  }

  public showMsg(): void {
    if (this.helpdeskForm.valid) {
      this.errorMsgTop = false;
    } else if (this.helpdeskFormDelete.valid) {
      this.errorMsgTopDel = false;
    }
  }

  private markAllDirty(control: AbstractControl): void {
    if (control.hasOwnProperty('controls')) {
        control.markAsDirty();
        const ctrl: any = <any>control;
        for (const inner in ctrl.controls) {
          if (ctrl.controls[inner]) {
            this.markAllDirty(ctrl.controls[inner] as AbstractControl);
          }
        }
    } else {
        (<FormControl>(control)).markAsDirty();
    }
  }

  public sendRequest(): void {
    const newBank = this.helpdeskForm.value;
    const currentCountry: NewNationalityObj  = this.countryList.find(country => country.Code === newBank.Country )
    newBank.Country = currentCountry;

    this.createRequestService.requestInProgress(true);

    if (this.bankChoice.value === 'add') {
      newBank.BankType = this.bankAccountTypes.find(e => e.ID === parseInt(this.bankAccountTypesControl.value, 10));
      this.userService.addBankAccount(newBank, this.attachments).subscribe(
        (res) => {
          this.createRequestService.requestInProgress(false);
          this.router.navigate(['/']);
        },
        (err) => {
          console.log('ERROR', err);
          this.createRequestService.requestInProgress(false);
          const popupOptions: PopupOptions = {
            title: this.translation.createRequest.bank.popInErr.titleAdd,
            text: err.Description,
            bgState: false,
            arrow: false,
            timer: 4000,
            position: {
                bottom: '14vh'
            }
          };
          this.popupService.setPopupState(true, popupOptions);
        }
      );
    } else if (this.bankChoice.value === 'delete') {
      this.userService.deleteBankAccount(this.bankAccountID.value, this.reason.value).subscribe(
        (res) => {
          this.createRequestService.requestInProgress(false);
          this.router.navigate(['/']);
        },
        (err) => {
          console.log('ERROR', err);
          this.createRequestService.requestInProgress(false);
          const popupOptions: PopupOptions = {
            title: this.translation.createRequest.bank.popInErr.titleRemove,
            text: err.Description,
            bgState: false,
            arrow: false,
            timer: 4000,
            position: {
                bottom: '14vh'
            }
          };
          this.popupService.setPopupState(true, popupOptions);
        }
      );
    }
  }
}
