import { Component, Input, OnChanges } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import cloneDeep from 'lodash-es/cloneDeep';
import { LangService } from '@services/references/lang.service';

@Component({
    selector: 'itx-helpdesk-create-select',
    templateUrl: './helpdesk-create-select.component.html',
    styleUrls: ['./helpdesk-create-select.component.scss']
})

export class HelpdeskCreateSelectComponent implements OnChanges {
    @Input() public parent: FormGroup;
    @Input() public control: FormControl;
    @Input() public selectDesc: string;
    @Input() public title: string;
    @Input() public icon: string = 'icon_priority';
    @Input('list')
    set list(list: any) {
        if (list) {
            this._list = list;
            /* setTimeout(() => {
              this.control.setValue(this._list[0][this.refVal]);
              this.currentVal = this.getControlValue(this._list[0][this.refVal]);
            }, 0); */
        }
    }
    @Input() public refVal: string = 'ID';

    public currentVal: string = "None";
    public translation;

    public _list: any;

    constructor(
        private langService: LangService
    ) {
        this.translation = this.langService.lang;
    }

    ngOnChanges(): void {
        if (this._list) {
            this.onChange(this.control.value);
        }
    }

    public onChange(value: any): void {
        this.currentVal = this.getControlValue(value);
    }

    public getControlValue(value: any): string {
        let newVal: any = '';
        let queryValue: any = value;
        if (value != null && value !== undefined && value[this.refVal]) {
            newVal = cloneDeep(this._list).find(el => el[this.refVal] === value[this.refVal]);
        } else {
            if (Number(value)) {
                queryValue = Number(value);
            }
            newVal = cloneDeep(this._list).find(el => el[this.refVal] === queryValue);
        }
        if (newVal) {
            return newVal.Text || newVal.Name;
        } else {
            return 'None';
        }
    }
}
