import { Component, OnInit, OnDestroy } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';

import { CreateRequestService } from './../../../core/services/create-request.service';
import { PopupService, PopupOptions } from './../../../core/services/popup.service';
import { UserService, UserInfos } from './../../../core/services/user.service';
import { LangService } from './../../../core/services/references/lang.service';
import { Subscription } from 'rxjs';
import { FileValidator } from '../../../shared/directives/file-validator.directive';
import { FileSizeValidator } from '@app/modules/shared/directives/file-size-validator.directive';
import { NgxSpinnerService } from 'ngx-spinner';
import { ButtonService } from "../../../shared/services/button.service";

@Component({
    selector: 'itx-helpdesk-create-passport',
    templateUrl: './helpdesk-create-passport.component.html',
    styleUrls: ['./helpdesk-create-passport.component.scss']
})
export class HelpdeskCreatePassportComponent implements OnInit, OnDestroy {

    public translation: any;
    public disableBtn: boolean = false;
    public helpdeskForm: FormGroup;

    public personID: FormControl;
    public passportNumber: FormControl;
    public passportExpDate: FormControl;
    public file: FormControl;

    public userInfos: UserInfos;
    public personIdToUpdate: number;
    public passportsList: any[] = [];
    public passportsListSelect: any[] = [];

    public errorMsgTop = false;

    private attachments: any;

    private sendRequestSubscription: Subscription;
    private showPopup: boolean;
    showSpinner1: boolean;

    constructor(
        private fb: FormBuilder,
        private createRequestService: CreateRequestService,
        private popupService: PopupService,
        private userService: UserService,
        private router: Router,
        private route: ActivatedRoute,
        private langService: LangService,
        private spinner: NgxSpinnerService,
        private button: ButtonService
    ) {
        this.translation = this.langService.lang;

        this.showPopup = true;

        this.personID = new FormControl(0, [Validators.required, this.validSelect]);
        this.passportNumber = new FormControl(null, [Validators.required]);
        this.passportExpDate = new FormControl(null, [Validators.required]);
        this.file = new FormControl(null, [FileValidator.validate, FileSizeValidator.validate]);
        this.helpdeskForm = this.fb.group({
            PersonID: this.personID,
            PassportNo: this.passportNumber,
            ExpirationDate: this.passportExpDate,
            Files: this.file
        });

        this.helpdeskForm.statusChanges.subscribe(status => {
            this.showMsg();
        });

        this.route.params.subscribe(params => {
            if (params['id']) {
                this.personIdToUpdate = params['id'];
                this.personID.setValue(this.personIdToUpdate);
            }
        });

        this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
            if (this.helpdeskForm.valid) {
                const checkRequestMsg: string = localStorage.getItem('checkRequestMsg');
                if (!res.popup || checkRequestMsg === '1') {
                    this.sendRequest();
                }
            } else {
                this.errorMsgTop = true;
                this.popupService.setPopupState(false);
                this.markAllDirty(this.helpdeskForm);
                window.scrollTo(0, 0);
            }
        });

        this.userService.userInfos$.subscribe(userInfos => {
            this.userInfos = userInfos;
            this.getPassports();
            this.hideSpinner();
            this.button.changeMessage(false);
        });
    }

    ngOnInit(): void {
        setTimeout(() => this.showSpinner(), 25);
        this.userService.getUserInfos().subscribe();
        this.button.changeMessage(true);
    }

    showSpinner(){
        this.showSpinner1 = true;
      }
      
      hideSpinner(){
        this.showSpinner1 = false;
      }

    ngOnDestroy(): void {
        this.sendRequestSubscription.unsubscribe();
    }

    public showMsg(): void {
        if (this.helpdeskForm.valid) {
            this.errorMsgTop = false;
        }
    }

    private checkForDuplicatePassports(passportsList: any, passport: any): boolean {
        return passportsList.filter(passportItem => passportItem['ID'] === passport['ID']).length > 0 ? false : true;
    }

    private getPassports(): void {
        this.passportsList = [];
        // Childrens passports
        this.userInfos.Children.forEach(children => {
            const childrenPassport: any = {
                Passport: children.Passport,
                ID: children.ID,
                Name: children.LastName + ' ' + children.FirstName + ' - ' + children.Passport.PassportNo
            };
            if (this.checkForDuplicatePassports(this.passportsList, childrenPassport)) {
                this.passportsList.push(childrenPassport);
            }
        });
        // Partner passport
        if (this.userInfos.Partner != null) {
            const partnerPassport: any = {
                Passport: this.userInfos.Partner.Passport,
                ID: this.userInfos.Partner.ID,
                Name: this.userInfos.Partner.LastName + ' ' + this.userInfos.Partner.FirstName + ' - ' + this.userInfos.Partner.Passport.PassportNo
            };
            if (this.checkForDuplicatePassports(this.passportsList, partnerPassport)) {
                this.passportsList.push(partnerPassport);
            }
        }
        // Personal passport
        // If ID = 0, the selectbox detected invalid value !
        const personalPassport: any = {
            Passport: this.userInfos.Passport,
            ID: -1,
            Name: this.userInfos.LastName + ' ' + this.userInfos.FirstName + ' - ' + this.userInfos.Passport.PassportNo
        };
        this.passportsList.push(personalPassport);
    }


    public getFilesToUpload(files: any): void {
        this.attachments = files;
    }

    private markAllDirty(control: AbstractControl): void {
        if (control.hasOwnProperty('controls')) {
            control.markAsDirty();
            const ctrl: any = <any>control;
            for (const inner in ctrl.controls) {
                if (ctrl.controls[inner]) {
                    this.markAllDirty(ctrl.controls[inner] as AbstractControl);
                }
            }
        } else {
            (<FormControl>(control)).markAsDirty();
        }
    }

    private validSelect(c: FormControl): any {
        const invalid: boolean = c.value === 0 || c.value === 'none' ? true : false;
        return invalid ? { 'invalid': { value: c.value } } : null;
    }

    public sendRequest(): void {
        const changePassport: any = {
            PassportNo: this.passportNumber.value,
            ExpirationDate: this.passportExpDate.value
        };

        this.createRequestService.requestInProgress(true);

        // Hack: PersonalID -1 to 0
        let personalID: number = parseInt(this.personID.value);
        if (personalID === -1) {
            personalID = 0;
        }

        this.userService.changePassport(changePassport, personalID, this.attachments).subscribe(
            (res) => {
                this.createRequestService.requestInProgress(false);
                this.router.navigate(['/']);
            },
            (err) => {
                this.createRequestService.requestInProgress(false);
                const popupOptions: PopupOptions = {
                    title: this.translation.createRequest.passport.popInErr.title,
                    text: err.Description,
                    bgState: false,
                    arrow: false,
                    timer: 4000,
                    position: {
                        bottom: '14vh'
                    }
                };
                this.popupService.setPopupState(true, popupOptions);
            }
        );
    }
}
