import { Component, OnInit, OnDestroy } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import { UserService, NewNationalityObj } from './../../../core/services/user.service';
import { PopupService, PopupOptions } from './../../../core/services/popup.service';
import { CreateRequestService } from './../../../core/services/create-request.service';
import { CountriesService } from './../../../core/services/references/countries.service';
import { LangService } from './../../../core/services/references/lang.service';
import { Subscription ,  combineLatest } from 'rxjs';
import { FileValidator } from '../../../shared/directives/file-validator.directive';
import { FileSizeValidator } from '@app/modules/shared/directives/file-size-validator.directive';

import { ButtonService } from "../../../shared/services/button.service";



@Component({
  selector: 'itx-helpdesk-create-adresses',
  templateUrl: './helpdesk-create-adresses.component.html',
  styleUrls: ['./helpdesk-create-adresses.component.scss']
})
export class HelpdeskCreateAdressesComponent implements OnInit, OnDestroy {

  public translation: any;
  public color:any;

  showSpinner1: boolean;
  

  public userInfos;
  public countryList: any[];
  public attachments: any;

  public helpdeskForm: FormGroup;
  public date: FormControl;
  public isPro: FormControl;
  public address: FormControl;
  public city: FormControl;
  public state: FormControl;
  public careof: FormControl;
  public pobox: FormControl;
  public zip: FormControl;
  public country: FormControl;
  public file: FormControl;

  private combineParamsAndUser;
  private sendRequestSubscription: Subscription;
  private showPopup: boolean;
  private typeAdresse: string;
  public showAddressChoice: boolean = true;
  public errorMsgTop: boolean = false;




  constructor(
    private fb: FormBuilder,
    private userService: UserService,
    private popupService: PopupService,
    private createRequestService: CreateRequestService,
    private router: Router,
    private countriesService: CountriesService,
    private langService: LangService,
    private route: ActivatedRoute,
    private button: ButtonService,
    
  ) {
    this.translation = this.langService.lang;

    this.showPopup = true;

    this.date = new FormControl(null, [Validators.required]);
    this.isPro = new FormControl('true');
    this.address = new FormControl(null, [Validators.required]);
    this.city = new FormControl(null, [Validators.required]);
    this.state = new FormControl(null);
    this.careof = new FormControl(null);
    this.pobox = new FormControl(null);
    this.zip = new FormControl(null, [Validators.required]);
    this.country = new FormControl(null, [Validators.required]);
    this.file = new FormControl("false", [FileValidator.validate, FileSizeValidator.validate]);

    this.helpdeskForm = this.fb.group({
      StartDate: this.date,
      IsProfessionalAddress: this.isPro,
      CareOf: this.careof,
      Address: this.address,
      POBox: this.pobox,
      City: this.city,
      State: this.state,
      ZIP: this.zip,
      Country: this.country,
      Files: this.file
    });

    this.helpdeskForm.statusChanges.subscribe(status => {
      this.showMsg();
    });

    this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
      if (this.helpdeskForm.valid) {
        const checkRequestMsg: string = localStorage.getItem('checkRequestMsg');
        if (!res.popup || checkRequestMsg === '1') {
          this.sendRequest();
        }
      } else {
        this.errorMsgTop = true;
        this.popupService.setPopupState(false);
        this.markAllDirty(this.helpdeskForm);
        window.scrollTo(0, 0);
      }
    });

    this.combineParamsAndUser = combineLatest(
      this.countriesService.countries$,
      this.userService.userInfos$,
      this.route.params
    );

    this.combineParamsAndUser.subscribe(([countriesList, userInfos, params]) => {
      this.typeAdresse = params['typeAdresse'];
      this.countryList = countriesList;
      this.userInfos = userInfos;
      if (this.typeAdresse !== undefined) {
        this.patchForm();
      }
     
      this.hideSpinner();
      this.button.changeMessage(false);
    });
  }

  ngOnInit(): void {

    

    setTimeout(() => this.showSpinner(), 25); 
    
 

    this.countriesService.getCountries().subscribe();
    this.userService.getUserInfos().subscribe();
    this.date.setValue(new Date());
    this.button.changeMessage(true);
  }

  showSpinner(){
    this.showSpinner1 = true;
  }
  
  hideSpinner(){
    this.showSpinner1 = false;
  }

 

  ngOnDestroy(): void {
    this.sendRequestSubscription.unsubscribe();
  }

  public getFilesToUpload(files: any): void {
    this.attachments = files;
  }

  public showMsg(): void {
    if (this.helpdeskForm.valid) {
      this.errorMsgTop = false;
    }
  }

  private markAllDirty(control: AbstractControl): void {
    if (control.hasOwnProperty('controls')) {
        control.markAsDirty();
        const ctrl: any = <any>control;
        for (const inner in ctrl.controls) {
          if (ctrl.controls[inner]) {
            this.markAllDirty(ctrl.controls[inner] as AbstractControl);
          }
        }
    } else {
        (<FormControl>(control)).markAsDirty();
    }
  }

  private patchForm(): void {
    let data = this.userInfos.ProfessionalContactInfo.Address;
    this.isPro.setValue(true);
    if (this.typeAdresse === 'perso') {
      data = this.userInfos.PersonalContactInfo.Address;
      this.isPro.setValue(false);
    }
    if (data === null) {
      return;
    }
    // this.isPro.disable();
    this.showAddressChoice = false;
    const currentCountry: NewNationalityObj = this.countryList.find(country => country.Name === data.Country.Name )
    if (currentCountry !== undefined) {
      this.country.setValue(currentCountry);
    }
    this.helpdeskForm.patchValue({
      CareOf: data.CareOf,
      Address: data.Address,
      POBox: data.POBox,
      City: data.City,
      State: data.State,
      ZIP: data.ZIP
    });
  }

  public sendRequest(): void {
    const newAddress: any = this.helpdeskForm.value;

    const code = this.country.value.Code ? this.country.value.Code : this.country.value;
    const currentCountry: NewNationalityObj  = this.countryList.find(country => country.Code == code);
    newAddress.Country = currentCountry;
    this.createRequestService.requestInProgress(true);

    this.userService.changeAddress(newAddress, this.attachments).subscribe(
      (res) => {
        this.createRequestService.requestInProgress(false);
        this.router.navigate(['/']);
      },
      (err) => {
        console.log('ERROR', err);
        this.createRequestService.requestInProgress(false);
        const popupOptions: PopupOptions = {
          title: this.translation.createRequest.address.popInErr.title,
          text: err.Description,
          bgState: false,
          arrow: false,
          timer: 4000,
          position: {
              bottom: '14vh'
          }
        };
        this.popupService.setPopupState(true, popupOptions);
      }
    );
  }

}
