import { Component, OnInit, OnDestroy, ElementRef, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Router } from '@angular/router';
import { FileSizeValidator } from '@app/modules/shared/directives/file-size-validator.directive';
import { PriorityLevelsService } from './../../../core/services/references/priority-levels.service';
import { CreateRequestService } from './../../../core/services/create-request.service';
import { PopupService, PopupOptions } from './../../../core/services/popup.service';
import { HelpdeskService } from './../../../core/services/helpdesk.service';
import { LangService } from './../../../core/services/references/lang.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'itx-helpdesk-create-other',
  templateUrl: './helpdesk-create-other.component.html',
  styleUrls: ['./helpdesk-create-other.component.scss']
})
export class HelpdeskCreateOtherComponent implements OnInit, OnDestroy {

  @ViewChild('mainForm') mainForm: ElementRef;

  public translation;

  public priorityLevels: string[];
  public helpdeskForm: FormGroup;
  public errorMsgTop: boolean = false;
  public descPlaceholder: string = '';

  public title: FormControl;
  public desc: FormControl;
  public priorityID: FormControl;
  public file: FormControl;

  private sendRequestSubscription: Subscription;
  private priorityLevelSubscription: Subscription;

  public attachments;

  private showPopup: boolean;

  constructor(
    private fb: FormBuilder,
    private priorityLevelsService: PriorityLevelsService,
    private createRequestService: CreateRequestService,
    private helpdesk: HelpdeskService,
    private popupService: PopupService,
    private router: Router,
    private langService: LangService
  ) {
    this.translation = this.langService.lang;
    // Detect <= IE11
    if (!document['documentMode']) {
      this.descPlaceholder = this.translation.createRequest.other.form.descPlaceholder;
    }
    this.showPopup = true;

    this.title = new FormControl(null, [ Validators.required ]);
    this.desc = new FormControl(null, [ Validators.required ]);
    this.priorityID = new FormControl(0, [ Validators.required, this.validSelect ]);
    this.file = new FormControl(null, [ FileSizeValidator.validate ]);
    this.helpdeskForm = this.fb.group({
      title: this.title,
      desc: this.desc,
      priorityID: this.priorityID,
      file: this.file,
    });

    this.helpdeskForm.statusChanges.subscribe(status => {
      this.showMsg();
    });

    this.priorityLevelSubscription = this.priorityLevelsService.priorityLevels$.subscribe(res => {
      this.priorityLevels = res;
    });

    this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
        if (this.helpdeskForm.valid) {
          const checkRequestMsg: string = localStorage.getItem('checkRequestMsg');
          if (!res.popup || checkRequestMsg === '1') {
            this.sendRequest();
          }
        } else {
          this.errorMsgTop = true;
          this.popupService.setPopupState(false);
          this.markAllDirty(this.helpdeskForm);
          window.scrollTo(0, 0);
        }
    });
  }

  ngOnInit(): void {
    this.priorityLevelsService.getPriorityLevels();
  }

  ngOnDestroy(): void {
    this.sendRequestSubscription.unsubscribe();
    this.priorityLevelSubscription.unsubscribe();
  }

  private markAllDirty(control: AbstractControl): void {
    if (control.hasOwnProperty('controls')) {
        control.markAsDirty();
        const ctrl: any = <any>control;
        for (const inner in ctrl.controls) {
          if (ctrl.controls[inner]) {
            this.markAllDirty(ctrl.controls[inner] as AbstractControl);
          }
        }
    } else {
        (<FormControl>(control)).markAsDirty();
    }
  }

  private validSelect(c: FormControl): any {
    const invalid: boolean = c.value === 0 || c.value === 'none' ? true : false;
    return invalid ? {'invalid': {value: c.value}} : null;
  }

  public getFilesToUpload(files: any): void {
    this.attachments = files;
  }

  public showMsg(): void {
    if (this.helpdeskForm.valid) {
      this.errorMsgTop = false;
    }
  }

  public sendRequest(): void {
    let body: any = {
      PriorityLevelID: this.priorityID.value,
      Title: this.title.value,
      Description: this.desc.value
    };

    this.createRequestService.requestInProgress(true);

    this.helpdesk.addHelpdeskRequest(body, this.attachments).subscribe(
      (res) => {
        this.createRequestService.requestInProgress(false);
        this.router.navigate(['/']);
      },
      (err) => {
        console.log('ERROR', err);
        this.createRequestService.requestInProgress(false);
        const popupOptions: PopupOptions = {
          title: this.translation.createRequest.other.popInErr.title,
          text: err.Description,
          bgState: false,
          arrow: false,
          timer: 4000,
          position: {
              bottom: '14vh'
          }
        };
        this.popupService.setPopupState(true, popupOptions);
      });
  }
}
