import { Component, OnDestroy } from '@angular/core';

import { CreateRequestService } from './../../../core/services/create-request.service';
import { PopupService } from './../../../core/services/popup.service';
import { Subscription } from 'rxjs';
import { LangService } from '@services/references/lang.service';

@Component({
  selector: 'itx-helpdesk-create',
  templateUrl: './helpdesk-create.page.html',
  styleUrls: ['./helpdesk-create.page.scss']
})
export class HelpdeskCreatePage implements OnDestroy {

  public translation: any;
  private showPopup: boolean;

  private sendRequestSubscription: Subscription;
  private btnClickSubscription: Subscription;

  constructor(
    private createRequestService: CreateRequestService,
    private popupService: PopupService,
    private langService: LangService
  ) {
    this.translation = this.langService.lang;
    this.showPopup = true;


    this.createRequestService.createRequest(true);

    this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
      if (res.sendRequest && res.popup) {
        this.popupService.setPopupState(
          true,
          {
            closeOnTap: false,
            title: this.translation.createRequest.other.sendRequestPopup.title,
            text: false,
            btn: {
              btnState: true,
              btnLabel: this.translation.createRequest.other.sendRequestPopup.yes,
            },
            btn2: {
              btnState: true,
              btnLabel: this.translation.createRequest.other.sendRequestPopup.no,
            },
            position: {
              bottom: '14vh'
            },
            bgState: true,
            checkbox: {
              checkboxState: true,
              checkboxLabel: this.translation.createRequest.other.sendRequestPopup.hideMessage
            }
          }
        );
      } else {
        this.popupService.setPopupState(false);
      }
    });

    this.btnClickSubscription = this.popupService.btnClick$.subscribe(res => {
      if (res === 'btn2') {
        this.popupService.setPopupState(false);
      } else {
        this.createRequestService.sendRequest(true, false);
      }
    });

    this.popupService.checkboxState$.subscribe(res => {
      if (res) {
        localStorage.setItem('checkRequestMsg', '1');
      } else {
        localStorage.setItem('checkRequestMsg', '0');
      }
    });
  }

  ngOnDestroy(): void {
    this.createRequestService.createRequest(false);
    this.popupService.setPopupState(false);

    this.sendRequestSubscription.unsubscribe();
    this.btnClickSubscription.unsubscribe();
  }

}
