import { Component, OnInit, OnDestroy } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import { validateEmail } from './../../../shared/utils';
import { CreateRequestService } from './../../../core/services/create-request.service';
import { GendersService } from './../../../core/services/references/genders.service';
import { PopupService, PopupOptions } from './../../../core/services/popup.service';
import { SuperiorService } from './../../../core/services/superior.service';
import { LangService } from './../../../core/services/references/lang.service';

@Component({
  selector: 'itx-helpdesk-create-superior',
  templateUrl: './helpdesk-create-superior.component.html',
  styleUrls: ['./helpdesk-create-superior.component.scss']
})
export class HelpdeskCreateSuperiorComponent implements OnInit, OnDestroy {

  public translation;

  public genderList: any[];
  public helpdeskForm: FormGroup;
  public errorMsgTop: boolean = false;

  public gender: FormControl;
  public firstname: FormControl;
  public lastname: FormControl;
  public email: FormControl;
  public phone: FormControl;

  private sendRequestSubscription;

  private showPopup: boolean;

  public attachments;

  constructor(
    private fb: FormBuilder,
    private createRequestService: CreateRequestService,
    private superiorService: SuperiorService,
    private popupService: PopupService,
    private router: Router,
    private genderService: GendersService,
    private langService: LangService
  ) {
    this.translation = this.langService.lang;
    this.showPopup = true;

    this.gender = new FormControl(null, [ Validators.required ]);
    this.firstname = new FormControl(null, [ Validators.required ]);
    this.lastname = new FormControl(null, [ Validators.required ]);
    this.email = new FormControl(null, [ Validators.required ], [ validateEmail.bind(this.email) ]);
    this.phone = new FormControl(null, [ Validators.required ]);
    this.helpdeskForm = this.fb.group({
      gender: this.gender,
      firstname: this.firstname,
      lastname: this.lastname,
      email: this.email,
      phone: this.phone,
    });

    this.helpdeskForm.statusChanges.subscribe(status => {
      this.showMsg();
    });

    this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
      if (this.helpdeskForm.valid) {
        const checkRequestMsg: string = localStorage.getItem('checkRequestMsg');
        if (!res.popup || checkRequestMsg === '1') {
          this.sendRequest();
        }
      }else  {
        this.errorMsgTop = true;
        this.popupService.setPopupState(false);
        this.markAllDirty(this.helpdeskForm);
        window.scrollTo(0, 0);
      }
    });

    this.genderService.genders$.subscribe(genderList => {
      this.genderList = genderList;
    });
  }

  ngOnInit(): void {
    this.genderService.getGenders();
  }

  ngOnDestroy(): void {
    this.sendRequestSubscription.unsubscribe();
  }

  public getFilesToUpload(files: any): void {
    this.attachments = files;
  }

  public showMsg(): void {
    if (this.helpdeskForm.valid) {
      this.errorMsgTop = false;
    }
  }

  public getGenderById(id: number): any {
    for (let i = this.genderList.length - 1; i >= 0; i--) {
      if (this.genderList[i].ID == id) {
        return this.genderList[i];
      }
    }
  }

  private markAllDirty(control: AbstractControl): void {
    if (control.hasOwnProperty('controls')) {
        control.markAsDirty();
        const ctrl: any = <any>control;
        for (const inner in ctrl.controls) {
          if (ctrl.controls[inner]) {
            this.markAllDirty(ctrl.controls[inner] as AbstractControl);
          }
        }
    } else {
        (<FormControl>(control)).markAsDirty();
    }
  }

  public sendRequest(): void {
    let body: any = {
      Gender: this.getGenderById(this.gender.value),
      FirstName: this.firstname.value,
      LastName: this.lastname.value,
      EmailAddress: this.email.value,
      PhoneNumber: this.phone.value
    };

    this.createRequestService.requestInProgress(true);

    this.superiorService.superiorRequest(body).subscribe(
      (res) => {
        this.createRequestService.requestInProgress(false);
        this.router.navigate(['/']);
      },
      (err) => {
        console.log('ERROR', err);
        this.createRequestService.requestInProgress(false);
        const popupOptions: PopupOptions = {
          title: this.translation.createRequest.superior.popInErr.title,
          text: err.Description,
          bgState: false,
          arrow: false,
          timer: 4000,
          position: {
              bottom: '14vh'
          }
        };
        this.popupService.setPopupState(true, popupOptions);
      });
  }
}
