import { Component, OnDestroy, OnInit, AfterViewChecked, AfterContentChecked  } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NavigationStart, Event, NavigationEnd } from '@angular/router';

import * as moment from 'moment';

import { validateEmail } from './../../../shared/utils';
import { MaritalStatusService } from './../../../core/services/references/marital-status.service';
import { GendersService } from './../../../core/services/references/genders.service';
import { LangService } from './../../../core/services/references/lang.service';
import { CreateRequestService } from './../../../core/services/create-request.service';
import { PopupOptions, PopupService } from './../../../core/services/popup.service';
import { UserService, NewNationalityObj } from './../../../core/services/user.service';
import { Subscription, combineLatest } from 'rxjs';
import { FileSizeValidator } from '@app/modules/shared/directives/file-size-validator.directive';
import { CountriesService } from '@app/modules/core/services/references/countries.service';
import { FileValidator } from '../../../shared/directives/file-validator.directive';
import { distinctUntilChanged } from 'rxjs/internal/operators/distinctUntilChanged';
import { NgxSpinnerService } from 'ngx-spinner';
import { ButtonService } from "../../../shared/services/button.service";





@Component({
    selector: 'itx-helpdesk-create-status',
    templateUrl: './helpdesk-create-status.component.html',
    styleUrls: ['./helpdesk-create-status.component.scss']
})
export class HelpdeskCreateStatusComponent implements OnInit, OnDestroy, AfterContentChecked {
    

    public translation: any;
    public disableBtn: boolean = false;

    public maritalStatusList: any[];
    public countriesList: any[];
    public genderList: any[];
    public helpdeskForm: FormGroup;
    public helpdeskFormFull: FormGroup;
    public userInfos;
    public errorMsgTop: boolean = false;
    public errorMsgTopFull: boolean = false;

    public changeDate: FormControl;
    public maritalStatus: FormControl;
    public gender: FormControl;
    public firstname: FormControl;
    public lastname: FormControl;
    public birthdate: FormControl;
    public nationality: FormControl;
    public phone: FormControl;
    public email: FormControl;
    public isExpatriate: FormControl;
    public passportNumber: FormControl;
    public expirationDate: FormControl;
    public file: FormControl;

    private attachments: any;

    private sendRequestSubscription: Subscription;
    private countryListSub: Subscription;
    private maritalStatusChangesSub: Subscription;
    private showPopup: boolean;

    private combineParamsAndMaritalStatus;
    showSpinner1: boolean;

    constructor(
        private fb: FormBuilder,
        private maritalStatusService: MaritalStatusService,
        private gendersService: GendersService,
        private userService: UserService,
        private createRequestService: CreateRequestService,
        private popupService: PopupService,
        private router: Router,
        private langService: LangService,
        private route: ActivatedRoute,
        private countriesService: CountriesService,
        private spinner: NgxSpinnerService,
        private button: ButtonService
     
    ) {
        
       
  
        
      
        
      
    
        this.translation = this.langService.lang;
        this.showPopup = true;

        this.maritalStatus = new FormControl(0, [Validators.required]);
        this.changeDate = new FormControl(null, [Validators.required]);
        this.gender = new FormControl(null, [Validators.required]);
        this.firstname = new FormControl(null, [Validators.required]);
        this.lastname = new FormControl(null, [Validators.required]);
        this.birthdate = new FormControl(null, [Validators.required]);
        this.nationality = new FormControl(null, [Validators.required]);
        this.isExpatriate = new FormControl('yes', [Validators.required]);
        this.passportNumber = new FormControl(null, []);
        this.expirationDate = new FormControl(null, []);
        this.file = new FormControl(null, [FileValidator.validate, FileSizeValidator.validate]);
        this.helpdeskForm = this.fb.group({
            Date: this.changeDate,
            File: this.file
        });
        this.helpdeskFormFull = this.fb.group({
            Nationality: this.nationality,
            IsExpatriate: this.isExpatriate,
            Gender: this.gender,
            FirstName: this.firstname,
            LastName: this.lastname,
            Birthdate: this.birthdate,
            PassportNo: this.passportNumber,
            ExpirationDate: this.expirationDate,
            File: this.file
        });

        this.helpdeskForm.statusChanges.subscribe(status => {
            this.showMsg();
        });

        this.helpdeskFormFull.statusChanges.subscribe(status => {
            this.showMsg();
        });

        this.countriesService.getCountries().subscribe(res => {
            this.countriesList = res;
        });

        this.maritalStatusChangesSub = this.maritalStatus.valueChanges.subscribe(val => {
            this.onMaritalStatusChange(val);
        });

        this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
            this.helpdeskForm.updateValueAndValidity();
            this.helpdeskFormFull.updateValueAndValidity();
            let currentFormValid: boolean = this.helpdeskForm.valid;
            if ((this.maritalStatus.value == '2' || this.maritalStatus.value == '3' || this.maritalStatus.value == '4') && this.helpdeskFormFull.invalid) {
                currentFormValid = false;
            }
          

            this.setUserCategoryValidators();

            console.log('currentFormValid:', currentFormValid);
            if (currentFormValid) {
                const checkRequestMsg: string = localStorage.getItem('checkRequestMsg');
                if (!res.popup || checkRequestMsg === '1') {
                    this.sendRequest();
                }
            } else {
                this.errorMsgTop = true;
                this.popupService.setPopupState(false);
                this.markAllDirty(this.helpdeskForm);
                if (this.maritalStatus.value == '2' || this.maritalStatus.value == '3' || this.maritalStatus.value == '4') {
                    this.errorMsgTopFull = true;
                    this.markAllDirty(this.helpdeskFormFull);
                }
                window.scrollTo(0, 0);
            }
        });


        
        this.combineParamsAndMaritalStatus = combineLatest(
            this.gendersService.genders$,
            this.maritalStatusService.maritalStatus$,
            this.userService.userInfos$,
            this.route.params
        );
        
      
        this.combineParamsAndMaritalStatus.subscribe(([genderList, maritalStatusList, userInfos, params]) => {
            this.maritalStatusList = maritalStatusList;
            this.genderList = genderList;
            this.userInfos = userInfos;
            if (this.userInfos.MaritalStatus) {
                this.maritalStatus.setValue(this.userInfos.MaritalStatus.ID);
            }
            this.patchForm();
                  
            
            this.hideSpinner();
            this.button.changeMessage(false);

            
        }); 
    }

    ngOnInit(): void {

       
        
        setTimeout(() => this.showSpinner(), 25);
       
        this.maritalStatusService.getMaritalStatus();
        this.gendersService.getGenders();
        this.userService.getUserInfos().subscribe();
        this.onMaritalStatusChange(false);
        this.button.changeMessage(true);

    }

    showSpinner(){
        this.showSpinner1 = true;
      }
      
      hideSpinner(){
        this.showSpinner1 = false;
      }

    ngAfterContentChecked(): void {
        this.makeFieldMandatory(this.passportNumber, this.expirationDate);
        this.makeFieldMandatory(this.expirationDate, this.passportNumber);
    }

    ngOnDestroy(): void {
        this.sendRequestSubscription.unsubscribe();
        this.maritalStatusChangesSub.unsubscribe();
        

    }

    


    setUserCategoryValidators() {

        
        const PassportNo = this.helpdeskFormFull.get('PassportNo').value;
        const ExpirationDate = this.helpdeskFormFull.get('ExpirationDate').value;
    
        let dataxx = JSON.parse(JSON.stringify(this.helpdeskFormFull.get('File').value));

         
        
        if ((PassportNo.length >0 || ExpirationDate.length >0) && ( dataxx == "false" || dataxx==null)) {
          
          this.helpdeskFormFull.patchValue({ File: null });
        }
    
        if (this.isEmpty(PassportNo) && this.isEmpty(ExpirationDate)) {
          this.helpdeskFormFull.patchValue({ File: "false" });
          
        }
        
      }

      isEmpty(value){
        return (value == null || value.length === 0);
      }

    public onMaritalStatusChange(val: any): void {
       // this.file.reset();
        if (!val) { val = this.maritalStatus.value; }
        if (val == '2' || val == '3' || val == '4') {
            this.validSelect(this.nationality);
            this.validSelect(this.gender);
            this.firstname.setValidators([Validators.required]);
            this.lastname.setValidators([Validators.required]);
            this.birthdate.setValidators([Validators.required]);
        }
        this.changeDate.setValidators([Validators.required]);
        //this.file.setValidators([]);
        //this.file.setErrors(null);
        this.helpdeskForm.updateValueAndValidity();
        this.helpdeskFormFull.updateValueAndValidity();
    }

    public getFilesToUpload(files: any): void {
        this.attachments = files;
    }

    public showMsg(): void {
        if (this.helpdeskForm.valid) {
            this.errorMsgTop = false;
        }
        if (this.helpdeskFormFull.valid) {
            this.errorMsgTopFull = false;
        }
    }

    private makeFieldMandatory(control1: FormControl, control2: FormControl): void {
        control1.valueChanges.pipe(distinctUntilChanged()).subscribe((data) => {
            if (data && data.length > 0) {
                control2.setValidators([Validators.required]);
            } else {
                control2.clearValidators();
            }
            control2.updateValueAndValidity();
        });
        control2.valueChanges.pipe(distinctUntilChanged()).subscribe((data) => {
            if (data && data.length > 0) {
                control1.setValidators([Validators.required]);
            } else {
                control1.clearValidators();
            }
            control1.updateValueAndValidity();
        });
    }

    public sendRequest(): void {
        const maritalStatus = this.maritalStatusList[this.maritalStatus.value - 1];
        let partnerInfo = null;

        this.createRequestService.requestInProgress(true);
        maritalStatus.Date = this.changeDate.value;

        if (this.maritalStatus.value == '2' || this.maritalStatus.value == '3' || this.maritalStatus.value == '4') {

            const code = this.nationality.value.Code ? this.nationality.value.Code : this.nationality.value;
            const currentNationality: NewNationalityObj = this.countriesList.find(country => country.Code == code);

            partnerInfo = {
                Nationality: currentNationality,
                IsExpatriate: this.isExpatriate.value === 'yes' ? true : false,
                Gender: this.genderList.filter(el => el.ID == this.gender.value)[0],
                FirstName: this.firstname.value,
                LastName: this.lastname.value,
                Birthdate: this.birthdate.value,
                Passport: {
                    PassportNo: this.passportNumber.value,
                    ExpirationDate: this.expirationDate.value
                }
            };
        }

        this.userService.changeMaritalStatus(maritalStatus, partnerInfo, this.attachments).subscribe(
            (res) => {
                this.createRequestService.requestInProgress(false);
                this.router.navigate(['/']);
            },
            (err) => {
                console.log('ERROR', err);
                this.createRequestService.requestInProgress(false);
                const popupOptions: PopupOptions = {
                    title: this.translation.createRequest.status.popInErr.title,
                    text: err.Description,
                    bgState: false,
                    arrow: false,
                    timer: 4000,
                    position: {
                        bottom: '14vh'
                    }
                };
                this.popupService.setPopupState(true, popupOptions);
            }
        );
    }


    private patchForm(): void {
        if (this.userInfos.Partner !== null) {
            this.helpdeskFormFull.patchValue({
                EmailAddress: this.userInfos.Partner.EmailAdress,
                PhoneNumber: this.userInfos.Partner.PhoneNumber,
                Nationality: this.userInfos.Partner.Nationality ? this.userInfos.Partner.Nationality : null,
                IsExpatriate: this.userInfos.Partner.IsExpatriate ? 'yes' : 'no',
                Gender: this.userInfos.Partner.Gender.ID,
                FirstName: this.userInfos.Partner.FirstName,
                LastName: this.userInfos.Partner.LastName,
                Birthdate: moment(this.userInfos.Partner.Birthdate).format('YYYY-MM-DD'),
                PassportNo: this.userInfos.Partner.Passport.PassportNo,
                ExpirationDate: this.userInfos.Partner.Passport.ExpirationDate ? moment(this.userInfos.Partner.Passport.ExpirationDate).format('YYYY-MM-DD') : null
            });
          
            
        }
        
    }
    private validSelect(c: FormControl): any {
        const invalid: boolean = c.value === 0 || c.value === 'none' ? true : false;
        return invalid ? { 'invalid': { value: c.value } } : null;
    }

    private markAllDirty(control: AbstractControl): void {
        if (control.hasOwnProperty('controls')) {
            control.markAsDirty();
            const ctrl: any = <any>control;
            for (const inner in ctrl.controls) {
                if (ctrl.controls[inner]) {
                    this.markAllDirty(ctrl.controls[inner] as AbstractControl);
                }
            }
        } else {
            (<FormControl>(control)).markAsDirty();
        }
    }
}
