import { Component, OnInit, OnDestroy, ElementRef, ViewChild } from '@angular/core';
import { AbstractControl, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import { ExpenseCategoriesService } from './../../../core/services/references/expense-categories.service';
import { ExpenseService } from './../../../core/services/expense.service';
import { UserService } from './../../../core/services/user.service';
import { CurrenciesService } from './../../../core/services/references/currencies.service';
import { PaymentMethodsService } from './../../../core/services/references/payment-methods.service';
import { PopupService, PopupOptions } from './../../../core/services/popup.service';
import { CreateRequestService } from './../../../core/services/create-request.service';
import { LangService } from './../../../core/services/references/lang.service';
import { Subscription } from 'rxjs';
import { FileValidator } from '../../../shared/directives/file-validator.directive';
import { FileSizeValidator } from '@app/modules/shared/directives/file-size-validator.directive';

@Component({
  selector: 'itx-helpdesk-create-expense',
  templateUrl: './helpdesk-create-expense.component.html',
  styleUrls: ['./helpdesk-create-expense.component.scss']
})
export class HelpdeskCreateExpenseComponent implements OnInit, OnDestroy {

  @ViewChild('mainForm') mainForm: ElementRef;

  public translation: any;

  public expenseCategories: any[];
  public expenseEvents: any[];
  public currencies: any[];
  public paymentMethods: any[];
  public helpdeskForm: FormGroup;
  public errorMsgTop: boolean = false;

  public userInfos: any;
  public bankAccounts: any[] = [];

  public expenseDate: FormControl;
  public expenseCategory: FormControl;
  public expenseEvent: FormControl;
  public price: FormControl;
  public currency: FormControl;
  public paymentMethod: FormControl;
  public expenseBankID: FormControl;
  public comment: FormControl;
  public file: FormControl;

  private attachments: any;
  private sendRequestSubscription: Subscription;
  private expenseCatSub: Subscription;
  private expenseEventsSub: Subscription;
  private currenciesSub: Subscription;
  private paymentsSub: Subscription;
  private userInfoSub: Subscription;
  private showPopup: boolean;

  constructor(
    private fb: FormBuilder,
    private expenseCategoriesService: ExpenseCategoriesService,
    private expenseService: ExpenseService,
    private currenciesService: CurrenciesService,
    private paymentMethodsService: PaymentMethodsService,
    private popupService: PopupService,
    private createRequestService: CreateRequestService,
    private router: Router,
    private userService: UserService,
    private langService: LangService
  ) {
    this.translation = this.langService.lang;
    this.showPopup = true;

    this.expenseDate = new FormControl(null, [Validators.required]);
    this.expenseCategory = new FormControl(0, [Validators.required, this.validSelect]);
    this.expenseEvent = new FormControl(0);
    this.price = new FormControl(null, [Validators.required]);
    this.currency = new FormControl(this.getCurrency(), [Validators.required, this.validSelect]);
    this.paymentMethod = new FormControl(0, [Validators.required, this.validSelect]);
    this.expenseBankID = new FormControl(0, [Validators.required, this.validSelect]);
    this.comment = new FormControl(null);
    this.file = new FormControl(null, [FileValidator.validate, FileSizeValidator.validate ]);
    this.helpdeskForm = this.fb.group({
      Date: this.expenseDate,
      Category: this.expenseCategory,
      Event: this.expenseEvent,
      Price: this.price,
      Currency: this.currency,
      PaymentMethod: this.paymentMethod,
      BankAccountID: this.expenseBankID,
      Comment: this.comment,
      Files: this.file
    });

    this.helpdeskForm.statusChanges.subscribe(status => {
      this.showMsg();
    });

    this.sendRequestSubscription = this.createRequestService.sendRequest$.subscribe(res => {
      if (this.helpdeskForm.valid) {
        const checkRequestMsg: string = localStorage.getItem('checkRequestMsg');
        if (!res.popup || checkRequestMsg === '1') {
          this.sendRequest();
        }
      } else {
        this.errorMsgTop = true;
        this.popupService.setPopupState(false);
        this.markAllDirty(this.helpdeskForm);
        window.scrollTo(0, 0);
      }
    });

    this.expenseCatSub = this.expenseCategoriesService.expenseCategories$.subscribe(res => {
      this.expenseCategories = res;
    });

    this.expenseEventsSub = this.expenseService.expenseEvents$.subscribe(events => {
      this.expenseEvents = events;
    });

    this.currenciesSub = this.currenciesService.currencies$.subscribe(currencies => {
      this.currencies = currencies;
      // const noCurrency = {Code: 'none', Name: 'None'};
      // this.currencies.unshift(noCurrency);
    });

    this.paymentsSub = this.paymentMethodsService.paymentMethods$.subscribe(paymentMethods => {
      this.paymentMethods = paymentMethods;
    });

    this.userInfoSub = this.userService.userInfos$.subscribe(userInfos => {
      this.userInfos = userInfos;
      this.getBankAccountsList();
    });
  }

  ngOnInit(): void {
    this.expenseCategoriesService.getExpenseCategories().subscribe();
    this.expenseService.getExpenseEvents().subscribe();
    this.currenciesService.getCurrencies().subscribe();
    this.paymentMethodsService.getPaymentMethods().subscribe();
    this.userService.getUserInfos(true).subscribe();
  }

  ngOnDestroy(): void {
    this.sendRequestSubscription.unsubscribe();
    this.expenseCatSub.unsubscribe();
    this.expenseEventsSub.unsubscribe();
    this.currenciesSub.unsubscribe();
    this.paymentsSub.unsubscribe();
    this.userInfoSub.unsubscribe();
  }

  private validSelect(c: FormControl): any {
      const invalid: boolean = c.value === 0 || c.value === 'none' ? true : false;
      return invalid ? {'invalidPayment': {value: c.value}} : null;
  }

  public getBankAccountsList(): void {
    this.userInfos.BankAccounts.forEach(bankAccount => {
      const temp: any = {
        'ID': bankAccount.ID,
        'Name': bankAccount.BankName + ', ' + bankAccount.IBAN + ' - ' + bankAccount.Currency
      };
      this.bankAccounts.push(temp);
    });
  }

  public getFilesToUpload(files: any): void {
    this.attachments = files;
  }

  public showMsg(): void {
    if (this.helpdeskForm.valid) {
      this.errorMsgTop = false;
    }
  }

  private markAllDirty(control: AbstractControl): void {
    if (control.hasOwnProperty('controls')) {
        control.markAsDirty();
        const ctrl: any = <any>control;
        for (const inner in ctrl.controls) {
          if (ctrl.controls[inner]) {
            this.markAllDirty(ctrl.controls[inner] as AbstractControl);
          }
        }
    } else {
        (<FormControl>(control)).markAsDirty();
    }
  }

  public sendRequest(): void {
    const newExpense: any = this.helpdeskForm.value;
    newExpense.Price = Math.ceil(newExpense.Price);

    this.saveCurrency(newExpense.Currency);
    this.createRequestService.requestInProgress(true);

    this.expenseService.addExpenseRequest(newExpense, this.attachments).subscribe(
      (res) => {
        this.createRequestService.requestInProgress(false);
        this.router.navigate(['/']);
      },
      (err) => {
        console.log('ERROR', err);
        this.createRequestService.requestInProgress(false);
        const popupOptions: PopupOptions = {
          title: this.translation.createRequest.expense.popInErr.title,
          text: err.Description,
          bgState: false,
          arrow: false,
          timer: 4000,
          position: {
              bottom: '14vh'
          }
        };
        this.popupService.setPopupState(true, popupOptions);
      }
    );
  }

  public saveCurrency(currency: string): void {
    localStorage.setItem('lastCurrency', currency);
  }

  public getCurrency(): any {
    const currency: string = localStorage.getItem('lastCurrency');
    if (currency) {
      return currency;
    } else {
      return 0;
    }
  }

}
