import { Component, ViewChild, HostListener, Output, EventEmitter, ElementRef, Input, OnInit, OnDestroy } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';
import { FormGroup, FormControl } from '@angular/forms';

import { LangService } from './../../../core/services/references/lang.service';
import { Subscription } from 'rxjs';


@Component({
  selector: 'itx-helpdesk-create-upload',
  templateUrl: './helpdesk-create-upload.component.html',
  styleUrls: ['./helpdesk-create-upload.component.scss'],
  animations: [
    trigger('fileState', [
      state('void', style({ transform: 'scale(0.5)', opacity: 0 })),
      state('*', style({ transform: 'scale(1)', opacity: 1 })),
      transition('void => *', animate('300ms 100ms ease-out')),
      transition('* => void', animate('250ms ease-in'))
    ])
  ]
})
export class HelpdeskCreateUploadComponent implements OnInit, OnDestroy {

  public translation: any;

  @Input() public parent: FormGroup;
  @Input() public control: FormControl;
  @Input() public title: string;
  @Input() public fileType: string;
  @Input() public multiple: boolean;
  @Input() public customClass: boolean;

  @ViewChild('fileInput') fileInput: ElementRef;

  public filesToUpload: Array<any> = [];
  @Output() filesChange: EventEmitter<any> = new EventEmitter<any>();

  private valueChanges$: Subscription = null;

  @HostListener('change', ['$event'])
  public onFileInputChange(event: any): void {
    for (let i = 0; i < event.target.files.length; i++) {
      const file: any = event.target.files[i];
      file.id = Math.round(Math.random() * 1000000);
      file.typeImage = false;
      if (file.type.startsWith('image/') === true) {
        file.typeImage = true;
      }
      file.oversize = false;
      if (!this.multiple) { this.filesToUpload = []; }
      this.filesToUpload.push(file);
    }
    this.updateFileList();
  }

  constructor(
    private langService: LangService
  ) {
    this.translation = this.langService.lang;
    this.multiple = true;
  }

  public ngOnInit(): void {
    this.valueChanges$ = this.control.valueChanges.subscribe(() => {
      this.updateView();
      if (this.control.errors !== null && this.control.errors['oversizeFileId']) {
        for (let i = 0; i < this.filesToUpload.length; i++) {
          if (this.control.errors['oversizeFileId'].indexOf(this.filesToUpload[i].id) !== -1) {
            this.filesToUpload[i].oversize = true;
          }
        }
      }
    });
  }

  public ngOnDestroy(): void {
    this.valueChanges$.unsubscribe();
  }

  private updateFileList(): void {
    this.fileInput.nativeElement.value = null;
    this.control.patchValue(this.filesToUpload);
    this.filesChange.emit(this.filesToUpload);
  }

  private updateView(): void {
    if (this.control.value === null && this.filesToUpload.length > 0) {
      this.filesToUpload = [];
      this.control.updateValueAndValidity();
    }
  }

  public removeFile(file): void {
    for (let i = 0; i < this.filesToUpload.length; i++) {
      if (this.filesToUpload[i].id === file.id) {
        this.filesToUpload.splice(i, 1);
      }
    }
    this.updateFileList();
  }

}
